<template>
    <b-container class="navigate-children-container">
        <b-row align-h="center">
            <b-col cols="12" class="border-radius6 padding-sm container-min-height">

                <div class="article-head padding-tb-xs">
                    <div class="article-head-title">

                        <div class="icon-size bg-box radius">
                            <div class="img-func icon-roundadd text-white radius" @click="chooseIcon"
                                 :style="'font-size:1.8rem;background-image: url(' + requestBody.iconImg + ')'"></div>
                            <input type="file" ref="iconfile" @change="fileHandle"
                                   accept="image/png,image/jpeg,image/gif,image/jpg" style="display: none">
                        </div>
                        <div class="title padding-lr-sm">
                            <input class="title-input bg-box" v-model="requestBody.title" type="text"
                                   placeholder="文章标题">

                            <div class="article-lable padding-xs text-blue">
                                <b-form-group>
                                    <b-form-checkbox-group
                                            id="checkbox-group-1"
                                            v-model="articleLableSelected"
                                            :options="options"
                                            name="flavour-1"
                                    ></b-form-checkbox-group>
                                </b-form-group>
                            </div>
                        </div>

                        <div class="article-head-lable bg-box radius icon-size text-blue padding-xs">
                            <div class="from-lable">
                                <b-form-group>
                                    <b-form-radio v-model="fromLableSelected" name="some-radios" value="ORIGINAL">原创
                                    </b-form-radio>
                                    <b-form-radio v-model="fromLableSelected" name="some-radios" value="REPRINT">转载
                                    </b-form-radio>
                                </b-form-group>
                            </div>
                        </div>


                    </div>
                </div>

                <quill-editor
                        ref="quillEditor"
                        class="editor-quill-editor border-radius6"
                        style="height: 100%;width: 100%;margin-bottom:160px;"
                        @change="onEditorChange($event)"
                        @blur="onEditorBlur($event)"
                        @focus="onEditorFocus($event)"
                        @ready="onEditorReady($event)"
                        :options="editorOption"
                >
                    <div id="toolbar" slot="toolbar"
                         style="position: relative !important;float:right;right:12px;top: 10px;">
                        <button @click="submit($event,'Enable')"
                                class="save-button cu-tag bg-gradual-blue shadow sm light round">save
                        </button>
                        <button @click="submit($event,'Disable')"
                                class="save-button cu-tag bg-gradual-blue shadow sm light round">issue
                        </button>
                    </div>
                </quill-editor>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
    import {quillEditor} from "vue-quill-editor";
    import {checkToken} from "@/utils"
    // import "quill/dist/quill.core.css";
    // import "quill/dist/quill.snow.css";
    // import "quill/dist/quill.bubble.css";
    // import "highlight.js/styles/an-old-hope.css";
    import hljs from "highlight.js/lib";

    export default {
        name: "writeArticleContainer",
        components: {
            quillEditor
        },
        props: {
            id: {
                type: String
            }
        },
        data() {
            return {
                requestBody: {
                    iconImg: "",
                    title: "",
                    articleLable: '',
                    articleFromLable: "",
                    content: "",
                    textContent: "",
                    authorId: "",
                    articleStatus: ''
                },
                fromLableSelected: "",
                articleLableSelected: [],
                options: [
                    {text: "Vue", value: "1"},
                    {text: "Java", value: "2"},
                    {text: "Spring", value: "3"},
                    {text: "Nginx", value: "4"}
                ],
                editorOption: {
                    placeholder: "开始你的创作之旅",
                    modules: {
                        toolbar: {
                            container: [
                                {header: 1},
                                {header: 2},
                                {size: ["small", false, "large", "huge"]},
                                "bold",
                                "italic",
                                "underline",
                                "blockquote",
                                {color: []},
                                {background: []},
                                "link",
                                {align: []},
                                {list: "ordered"},
                                {list: "bullet"},
                                "image",
                                "code-block"
                            ]
                        },
                        syntax: {
                            highlight: text => {
                                return hljs.highlightAuto(text).value

                            }
                        }
                    }
                }
            };
        },
        methods: {

            submit(event, issueStatus) {
                if (!checkToken()) {
                    this.$router.push({name: "Login"})
                    return
                }
                // //解析提交数据
                let body = this.resolveParmas(event, issueStatus);
                if (body) {
                    //提交数据
                    this.$http({
                        url: this.$http.baseUrl(`/article/${!this.$route.query.id ? 'save' : 'update'}`),
                        method: 'post',
                        data: body
                    }).then(({data}) => {
                        if (data && data.code === 0) {
                            this.$router.push({path: `/article/detail/${data.id}`})
                        } else {
                            return Promise.reject(data.msg)
                        }
                    }).catch(msg => {
                        this.$bvToast.toast(msg.toString(), {
                            title: '错误',
                            toaster: 'b-toaster-top-center',
                            autoHideDelay: 5000
                        })
                    })
                } else {
                    this.$bvToast.toast('数据未改动', {
                        title: '提示',
                        toaster: 'b-toaster-top-center',
                        autoHideDelay: 5000
                    })
                }
            },

            chooseIcon() {
                this.$refs.iconfile.click()
            },
            fileHandle(event) {
                let that = this
                let reader = new FileReader();
                reader.readAsDataURL(event.target.files[0]); // 读出 base64
                reader.onloadend = () => {
                    that.requestBody.iconImg = reader.result;
                }
            },
            onEditorChange({quill, html, text}) {
                this.requestBody.content = html
                this.requestBody.textContent = text
            },
            onEditorBlur(event) {

            },
            onEditorFocus(event) {

            },
            onEditorReady(event) {

            },

            /**
             * 格式化请求参数
             */
            resolveParmas(event, issueStatus) {
                this.requestBody.articleLable = JSON.stringify(this.articleLableSelected)
                this.requestBody.articleFromLableEnum = this.fromLableSelected
                this.requestBody.authorId = unescape(this.$cookie.get('user')).id
                this.requestBody.articleStatusEnum = issueStatus
                return this.requestBody
            }
        },
    };
</script>

<style scoped>

    .save-button {
        border: none;
        width: 56px !important;
        height: 28px !important;
    }

    .title {
        flex: 1;
    }

    .title-input {
        outline: none;
        padding: 0 40px 0 20px;
        width: 100%;
        height: 32px;
        font-size: 14px;
        border: 1px solid #eee;
        border-radius: 40px;
    }

    .article-head {
        width: 100%;
    }

    .article-head-title {
        display: -webkit-box;
    }

    .article-head-lable {
        display: flex;
    }

    .icon-size {
        height: 80px;
        width: 80px;
    }

    .img-func {
        cursor: pointer;
        position: relative !important;
        float: right;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        -moz-background-size: 100% 100%;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 0;
    }
</style>
<style>
    .editor-quill-editor .ql-toolbar {
        border: none !important;
        border-top: 5px ridge #67d6f5 !important;
        border-radius: 6px !important;
        background: rgba(255, 255, 255, 0.9);
        margin-bottom: 3px;
        -webkit-box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.09);
        box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.12);
    }

    .editor-quill-editor .ql-container {
        border: none !important;
        border-radius: 6px !important;
        background: rgba(255, 255, 255, 0.8);
        -webkit-box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.09);
        box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.09);
    }

    .editor-quill-editor .ql-editor {
        position: absolute;
        width: 100%;
        overflow-y: scroll;
    }

    .editor-quill-editor .ql-formats {
        max-width: 65% !important;
    }

    .b-toaster-top-center {
        position: fixed;
        left: .5rem;
        right: .5rem;
        margin: 0;
        padding-top: 72px;
        height: 0;
        overflow: visible;
    }

    .b-toaster {
        position: absolute;
        text-align: center;
        top: 0;
        z-index: 9999999999999;
    }

    .b-toast {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

    .toast {
        opacity: 1 !important;
        min-width: 30%;
    }

    .toast-header {
        border-top: 5px ridge #67d6f5 !important;
    }

</style>